.popup {
	display: none;
	position: fixed;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	z-index: 15;
	overflow-y: auto;
	background: rgba(#000, 0.5);
	@include m {
		overflow-y: scroll; }
	&__overlay {
		height: 100%;
		position: absolute;
		width: 100%;
		@include m {
			display: none; } }
	&__content {
		padding: 30px 20px 45px;
		left: 50%;
		top: 15%;
		position: absolute;
		transform: translateX(-50%);
		width: 400px;
		background-color: white;
		text-align: center;
		margin-bottom: 2vh;
		@include m {
			overflow-y: scroll;
			width: 100%;
			height: 100%;
			border: none;
			position: relative;
			top: 0;
			left: 0;
			transform: translate(0, 0);
			padding: 60px 20px 25px;
			margin-bottom: 0; } }
	&__close {
		position: absolute;
		width: 16px;
		height: 16px;
		top: 19px;
		right: 19px;
		cursor: pointer;
		@include trans;
		.icon {
			fill: $grey;
			width: 100%;
			height: 100%; }

		&:before {
			content: '';
			@include vcenter;
 }			// +s(close)
		&:hover {
 } }			// background: $orange3
	&_cities {
		.popup__content {
			padding: 50px;
			text-align: left;
			width: 1000px; } }
	&--map {
		.popup__content {
			width: 1000px;
			padding: 0;
			border-radius: 5px; }
		.gmap {
			height: 647px;
			border-radius: 5px; }
		.popup__close {
			right: -26px;
			top: 0; } } }
.baloon {
	width: 220px;
	border-radius: 5px;
	background: $white;
	text-align: center;
	padding-top: 16px;
	padding-bottom: 20px;
	position: relative;

	&:after {
		position: absolute;
		content: '';
		width: 18px;
		height: 9px;
		bottom: -9px;
		left: 50%;
		transform: translateX(-50%);
		@include arr(18, 9, $white, b); }
	&__content {}
	&__title, &__price {
		font-size: 14px;
		color: $cyan;
		@include PExtra; }
	&__title {
		margin-bottom: 10px; }
	&__text {
		@include PSemi;
		font-size: 14px;
		margin-bottom: 2px; }
	.rate-stars {
		margin-bottom: 18px; } }
