.pcities__item .item { display: flex; align-items: flex-start; }

.pcities__item .item__letter { font-family: Panton-Bold, Arial, sans-serif; font-size: 20px; margin-right: 10px; }

.pcities__item .item__list { text-align: left; }

.pcities__item .item__list li { font-size: 14px; }

.pcities__item .item__list li a { color: #14c1e5; }

.goodies { background-color: #14c1e5; text-align: center; padding-top: 66px; padding-bottom: 80px; }

.goodies__title { margin-bottom: 10px; letter-spacing: -.7px; }

.goodies__descr { font-size: 20px; color: #fff; line-height: 1.25; max-width: 780px; margin: 0 auto 74px; letter-spacing: -0.25px; font-family: OpenSans-Regular, Arial, sans-serif; }

.goodies__list { max-width: 1000px; margin: 0 auto; }

.goodies__wrapper { margin: 0 -15px; font-size: 0; text-align: center; }

.goodies__item { padding: 0 15px; display: inline-block; vertical-align: top; width: 25%; }

.goodies__text { display: inline-block; position: relative; letter-spacing: -0.5px; }

.goodies__text span { width: 22px; height: 22px; border-radius: 50%; background-color: #14c1e5; position: relative; display: inline-block; vertical-align: middle; margin-right: 10px; }

.goodies__text span .icon { position: absolute; top: 50%; left: 50%; margin-left: -2px; margin-top: -4px; fill: #fff; font-size: 8px; }

.goodies__link { background-color: #FFF; font-family: Panton-SemiBold, Arial, sans-serif; font-size: 20px; position: relative; border-radius: 28px; box-shadow: 0 4px 27px rgba(20, 193, 229, 0.47); color: #14c1e5; display: flex; justify-content: center; width: 100%; padding: 15px; }

.section-double-slider { background: #ffffff; padding-top: 46px; }

.section-double-slider__title { margin-bottom: 30px; }

.section-double-slider__inner { padding: 0 44px; position: relative; }

.section-double-slider .btnPrev, .section-double-slider .btnNext { bottom: auto; top: 50%; transform: translateY(-50%); }

.section-double-slider .btnPrev { left: -44px; }

.section-double-slider .btnNext { right: -44px; }

.section-double-slider .rate-wrapper { max-width: 252px; }

.tablelist { border-radius: 3px; overflow: hidden; }

.tablelist__row { display: flex; justify-content: space-between; align-items: center; min-height: 30px; padding: 0 19px; }

.tablelist__row:nth-child(odd) { background-color: #f6f9f9; }

.tablelist__row:nth-child(even) { background-color: #edeff0; }

.tablelist__col { font-family: OpenSans-SemiBold, Arial, sans-serif; font-size: 14px; }

.badge-slider { display: flex; justify-content: space-between; }

.badge-slider__left { width: 30.21429%; }

.badge-slider__right { width: 67.64286%; }

.badge-slider__right .slick-track { padding: 22px 10px 15px; height: 100%; }

.badge-slider__titlebox { background-color: #14c1e5; border-top-right-radius: 6px; border-bottom-right-radius: 6px; padding: 46px 43px 40px; min-height: 215px; }

.badge-slider__title { color: #ffffff; font-family: Panton-SemiBold, Arial, sans-serif; margin-bottom: 19px; max-width: 230px; }

.badge-slider__slide { height: 192px; padding-right: 40px; }

.badge-slider__slide .sale-block { height: 100%; box-shadow: 0 4px 27px rgba(20, 193, 229, 0.22); }

.badge-slider__slide .sale-block__title { height: calc(100% - 40px); overflow: hidden; text-overflow: ellipsis; display: -webkit-box; -webkit-line-clamp: 5; -webkit-box-orient: vertical; font-size: 18px; color: #000; }

.badge-slider__slide .sale-block__places-name { position: absolute; width: 100%; left: 0; padding: 0 20px; bottom: 20px; }

.btn-prev, .btn-next { background-color: #ffffff; border: 0; border-radius: 50%; width: 22px; height: 22px; display: inline-block; vertical-align: middle; font-size: 9px; position: relative; }

.btn-prev svg, .btn-next svg { width: 1em; height: 1em; position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); }

.btn-next svg { margin-left: 1px; }

.btn-prev { margin-right: 10px; }

.btn-prev svg { margin-left: -1px; }

.button { color: #fff; font-size: 15px; border-radius: 30px; padding: 12px 21px 11px; font-family: Panton-SemiBold, Arial, sans-serif; display: inline-block; transition: all 0.2s ease; }

.button_cyan { background-color: #14c1e5; }

.button_hero { padding: 12px 32px 8px; border: 1px solid #14c1e5; color: #14c1e5; letter-spacing: 0.5px; font-family: Panton-Bold, Arial, sans-serif; }

.button_hero:hover { background-color: #14c1e5; color: #fff; }

.button_hero_sm { padding: 12px 22px 8px; }

.button_border-white { background-color: transparent; border: 1px solid #fff; color: #fff; padding: 11px 21px 10px; }

.button_border-white:hover { background-color: #fff; color: #14c1e5; }

.button_primary { border: 1px solid #14c1e5; font-family: Panton-Bold, Arial, sans-serif; padding: 18px 73px; font-size: 20px; }

@media only screen and (max-width: 767px) { .button_primary { width: 100%; } }

.button_border { background-color: transparent; border: 1px solid #14c1e5; color: #14c1e5; }

.button_border:hover { background-color: #14c1e5; color: #fff; }

.btn { padding: 10px 15px 11px; border-radius: 20px; color: #ffffff; font-size: 16px; border: none; display: inline-block; vertical-align: top; appearance: none; font-family: Panton-Bold, Arial, sans-serif; text-align: center; }

.btn--w-240 { min-width: 240px; }

.btn--pink { background-color: #ff016e; }

.btn--green { background-color: #74d15e; font-size: 14px; padding: 11px 15px 12px; }

.btnNext, .btnPrev { width: 28px; height: 28px; background-color: #f6f9f9; cursor: pointer; text-align: center; border-radius: 50%; position: absolute; z-index: 1; }

.btnNext i, .btnPrev i { position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); }

.btnNext svg, .btnPrev svg { width: 7px; height: 12px; fill: #14c1e5; }

.btnNext.slick-disabled, .btnPrev.slick-disabled { cursor: default; user-select: none; user-select: none; }

.more-link { color: #14c1e5; font-family: OpenSans-Light, Arial, sans-serif; position: relative; display: inline-block; }

.more-link span { margin-left: 20px; }

.more-link__icon { width: 16px; height: 16px; border-radius: 50%; background-color: #9ce3f1; display: block; position: absolute; top: 50%; transform: translateY(-50%); left: 0; }

.more-link__icon .icon { display: block; font-size: 7px; fill: #fff; position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); margin-left: 1px; }

.more-link--sm { font-size: 14px; }

.slick-dots { text-align: center; font-size: 0; }

.slick-dots li { display: inline-block; vertical-align: top; margin: 5px 10px; }

.slick-dots li button { appearance: none; border: none; width: 7px; height: 7px; background-color: #dcdddd; border-radius: 50%; position: relative; font-size: 0; transition: all 0.2s ease; }

.slick-dots li button:before { content: ''; position: absolute; top: -3px; bottom: -3px; left: -3px; right: -3px; }

.slick-dots li.slick-active button { background-color: #14c1e5; }

.textfield { border: 1px solid #b8b8b8; border-radius: 20px; padding: 12px 16px 8px; position: relative; font-family: Panton-Regular, Arial, sans-serif; font-size: 14px; color: #1a1a1a; width: 100%; }

.textfield::placeholder { color: #b8b8b8; }

.input { font-size: 16px; color: #1a1a1a; width: 100%; box-shadow: 0 1px 9px rgba(67, 158, 167, 0.47); border-radius: 25px; padding: 20px 22px 11px; background-color: #fff; font-family: Panton-Regular, Arial, sans-serif; }

.input::placeholder { color: #b8b8b8; }

.field { position: relative; }

.input-icon { position: absolute; top: 50%; right: 26px; margin-top: -9px; }

.input-icon .icon { stroke: #c8c8c8; height: 12px; width: 18px; stroke-width: 2px; }

.form__select { display: none; }

.form__field { display: inline-block; }

.form__field--lg { width: 340px; }

@media only screen and (max-width: 1023px) { .form__field--lg { width: 300px; } }

@media only screen and (max-width: 767px) { .form__field--lg { width: 100%; } }

.form__field--lg .custom-select { border-radius: 30px; }

.custom-select { display: inline-block; position: relative; cursor: pointer; color: #b8b8b8; background-color: #ffffff; font-family: Panton-Regular, Arial, sans-serif; width: 100%; }

.custom-select__field { padding: 22px 10px 19px; }

.custom-select__dropdown { padding: 20px; background-color: #ffffff; line-height: 30px; border-radius: 30px; transition: all 0.2s; position: absolute; bottom: 0; transform: translateY(100%); visibility: hidden; opacity: 0; width: 100%; }

.custom-select.is-open { border-radius: 30px 30px 0 0; }

.custom-select.is-open .custom-select__dropdown { border-top: 1px solid #dce2e3; border-radius: 0 0 30px 30px; z-index: 1; visibility: visible; opacity: 1; }

.custom-select.is-open .custom-select__field .icon { transform: translateY(-50%) rotate(180deg); }

.custom-select.is-open .custom-select__field .icon-label { transform: translateY(-50%) rotate(0); }

.custom-select__list li:hover { color: #14c1e5; }

.custom-select__field { margin-right: 30px; margin-left: 29px; padding-left: 24px; position: relative; }

.custom-select__field .icon { transition: all 0.3s; height: 12px; width: 18px; stroke: #b8b8b8; position: absolute; right: 0; top: 50%; transform: translateY(-50%); }

.custom-select__field .icon-label { height: 14px; width: 17px; fill: #b8b8b8; left: 0; }

.date .custom-select { border-radius: 25px; }

.date .custom-select select { display: none; }

.date .custom-select.is-open { border-radius: 25px 25px 0 0; }

.date .custom-select__field { border-radius: 25px; padding: 20px 22px 11px; margin: 0; }

.date .custom-select__field .icon { right: 14px; }

.date .custom-select__text { text-align: left; font-size: 16px; }

.range__in { background-color: #dce2e3; border-radius: 3px; border: none; box-shadow: none; height: 5px; }

.range__in .noUi-connect { background-color: #14c1e5; background-image: -webkit-linear-gradient(bottom, #14c1e5 0%, #3bd7f7 100%); background-image: -o-linear-gradient(bottom, #14c1e5 0%, #3bd7f7 100%); background-image: linear-gradient(to top, #14c1e5 0%, #3bd7f7 100%); }

.range__in .noUi-handle { border-color: #14c1e5; border-radius: 50%; height: 17px; width: 17px; }

.range__in .noUi-handle:before, .range__in .noUi-handle:after { content: none; }

.range-inputs { margin-bottom: 17px; white-space: nowrap; }

.range-inputs input { background-color: #ffffff; border: 1px solid #dce2e3; border-radius: 15px; display: inline-block; font-size: 14px; padding: 8px 10px 4px; text-align: center; width: 100px; font-family: Panton-SemiBold, Arial, sans-serif; }

.range-inputs input:placeholder { font-size: 14px; font-family: Panton-SemiBold, Arial, sans-serif; }

.range-inputs__delimiter { background-color: #1a1a1a; display: inline-block; height: 2px; margin: 0 3px; width: 7px; vertical-align: middle; }

.main .section-video { padding: 125px 10px 100px; }

.main .faq-section { padding: 100px 10px; }

.main .faq-section__title { text-align: center; }

.main .faq-section__inner { margin: 0 auto; }

.main .badge-slider { padding-top: 90px; }
