.textfield {
	border: 1px solid $grey;
	border-radius: 20px;
	padding: 12px 16px 8px;
	position: relative;
	@include PReg;
	font-size: 14px;
	color: $black;
	width: 100%;
	@include placeholder {
		color: $grey; } }

.input {
	font-size: 16px;
	color: $black;
	width: 100%;
	box-shadow: 0 1px 9px rgba(67, 158, 167, 0.47);
	border-radius: 25px;
	padding: 20px 22px 11px;
	background-color: #fff;
	@include PReg;
	@include placeholder {
		color: $grey; } }

.field {
	position: relative; }

.input-icon {
	position: absolute;
	top: 50%;
	right: 26px;
	margin-top: -9px;
	.icon {
		stroke: #c8c8c8;
		height: 12px;
		width: 18px;
		stroke-width: 2px; } }


