.header {
	padding-top: 17px;
	padding-bottom: 11px;
	box-shadow: 0 4px 16px rgba(0, 0, 0, 0.05);
	&__in {
		@include clr; }
	&__logo {
		float: left;
		width: 170px;
		img {
			max-width: 100%;
			height: auto; } }
	&__select {
		float: left;
		margin-left: 50px;
		width: 205px;
		margin-top: 8px; }
	&__menu {
		float: left;
		margin-left: 50px;
		padding-top: 15px; }
	&__auth {
		float: right;
		margin-left: 50px;
		padding-top: 6px;
		@include clr; }
	&__burger {
		float: right;
		padding-top: 14px;
		margin-left: 20px; } }

.cities-select {
	border-radius: 20px;
	border: 1px solid $cyan;
	position: relative;
	&__field {
		font-size: 15px;
		@include PSemi;
		color: $black;
		letter-spacing: 0.07px;
		padding: 12px 30px 8px 43px; }
	&__label {
		position: absolute;
		top: 50%;
		left: 18px;
		transform: translateY(-50%);
		margin-top: 2px;
		.icon {
			fill: $cyan;
			font-size: 18px; } }
	&__arrow {
		position: absolute;
		top: 14px;
		right: 22px;
		transform: rotate(-45deg);
		width: 8px;
		height: 8px;
		border-left: 2px solid $cyan;
		border-bottom: 2px solid $cyan; } }

